}

input[type="checkbox"]+label:before,
input[type="radio"]+label:before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background-color: #fff;
  border: 1px solid #EBF2F3;
